body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --max-width: 1100px;
  --border-radius: 12px;
  --font-mono: ui-monospace, Menlo, Monaco, 'Cascadia Mono', 'Segoe UI Mono',
    'Roboto Mono', 'Oxygen Mono', 'Ubuntu Monospace', 'Source Code Pro',
    'Fira Mono', 'Droid Sans Mono', 'Courier New', monospace;

  --foreground-rgb: 0, 0, 0;
  --foreground-light-rgb: 0,0,0;
  --foreground-dark-rgb: 255,255,255;

  --background-start-rgb: 214, 219, 220;
  --background-start-dark-rgb: 28, 43, 107;
  --background-start-light-rgb: 214, 219, 220;
  --background-startclose-dark-rgb: 16, 24, 60;
  --background-startclose-light-rgb: 235, 235, 237;
  --background-end-rgb: 255, 255, 255;
  --background-end-dark-rgb: 0, 0, 0;
  --background-end-light-rgb: 255,255,255;

  --primary-glow: conic-gradient(
    from 180deg at 50% 50%,
    #16abff33 0deg,
    #0885ff33 55deg,
    #54d6ff33 120deg,
    #0071ff33 160deg,
    transparent 360deg
  );
  --primary-glow-dark: radial-gradient(rgba(1, 65, 255, 0.4), rgba(1, 65, 255, 0));
  --primary-glow-light: conic-gradient(
    from 180deg at 50% 50%,
    #16abff33 0deg,
    #0885ff33 55deg,
    #54d6ff33 120deg,
    #0071ff33 160deg,
    transparent 360deg
  );
  --secondary-glow: radial-gradient(
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 0)
  );
  --secondary-glow-dark: linear-gradient(
    to bottom right,
    rgba(1, 65, 255, 0),
    rgba(1, 65, 255, 0),
    rgba(1, 65, 255, 0.3)
  );
  --secondary-glow-light: radial-gradient(
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 0)
  );

  --tile-start-rgb: 239, 245, 249;
  --tile-start-dark-rgb: 2, 13, 46;
  --tile-start-light-rgb: 239, 245, 249;
  --tile-end-rgb: 228, 232, 233;
  --tile-end-dark-rgb: 2, 5, 19;
  --tile-end-light-rgb: 228, 232, 233;
  --tile-border: conic-gradient(
    #00000080,
    #00000040,
    #00000030,
    #00000020,
    #00000010,
    #00000010,
    #00000080
  );
  --tile-border-dark: conic-gradient(
    #ffffff80,
    #ffffff40,
    #ffffff30,
    #ffffff20,
    #ffffff10,
    #ffffff10,
    #ffffff80
  );
  --tile-border-light: conic-gradient(
    #00000080,
    #00000040,
    #00000030,
    #00000020,
    #00000010,
    #00000010,
    #00000080
  );

  --callout-rgb: 238, 240, 241;
  --callout-dark-rgb: 20, 20, 20;
  --callout-light-rgb: 238, 240, 241;
  --callout-border-rgb: 172, 175, 176;
  --callout-border-black-rgb: 108, 108, 108;
  --callout-border-light-rgb: 172, 175, 176;
  --card-rgb: 180, 185, 188;
  --card-dark-rgb: 100, 100, 100;
  --card-light-rgb: 180, 185, 188;
  --card-border-rgb: 131, 134, 135;
  --card-border-dark-rgb: 200, 200, 200;
  --card-border-light-rgb: 131, 134, 135;
}

@media (prefers-color-scheme: dark) {
  :root {
    --foreground-rgb: 255, 255, 255;
    --background-start-rgb: 28, 43, 107;
    --background-startclose-rgb: 16, 24, 60;
    --background-end-rgb: 0, 0, 0;

    --primary-glow: radial-gradient(rgba(1, 65, 255, 0.4), rgba(1, 65, 255, 0));
    --secondary-glow: linear-gradient(
      to bottom right,
      rgba(1, 65, 255, 0),
      rgba(1, 65, 255, 0),
      rgba(1, 65, 255, 0.3)
    );

    --tile-start-rgb: 2, 13, 46;
    --tile-end-rgb: 2, 5, 19;
    --tile-border: conic-gradient(
      #ffffff80,
      #ffffff40,
      #ffffff30,
      #ffffff20,
      #ffffff10,
      #ffffff10,
      #ffffff80
    );

    --callout-rgb: 20, 20, 20;
    --callout-border-rgb: 108, 108, 108;
    --card-rgb: 100, 100, 100;
    --card-border-rgb: 200, 200, 200;
  }
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  color: rgb(var(--foreground-rgb));
  background: radial-gradient(
      circle at 96% 8%,
      rgb(var(--background-start-rgb)),
      rgb(var(--background-startclose-rgb))23%,
      rgb(var(--background-end-rgb)) 100%
    );
}

a {
  color: inherit;
  text-decoration: none;
}

@media (prefers-color-scheme: dark) {
  html {
    color-scheme: dark;
  }
}